<template>
    <div class="container">
        <h3 class="bg-primary text-white">Crear Usuario</h3>
        <b-card bg-variant="dark">
            <form @submit.prevent="agregarUsuario()">
                <div class="row">
                    <div class="col-12">
                        <h5 class="text-white">Datos Personales</h5>
                        <div class="row">
                            <div class="col-6">
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Nombres : " label-for="input-nombre">
                                    <b-form-input id="input-nombre" size="sm" type="text" class="form-control my-2" placeholder="Ingrese su nombre" v-model="usuario.nombre"></b-form-input>
                                </b-form-group>
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Apellidos : " label-for="input-apellido">
                                    <input id="input-apellido" size="sm" type="text" class="form-control my-2" placeholder="Ingrese sus apellidos" v-model="usuario.apellido">
                                </b-form-group>
                            </div>
                                <div class="col-6">
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Cdula : " label-for="input-cedula">
                                    <input id="input-cedula" size="sm" type="text" class="form-control my-2" placeholder="ingrese su cdula" v-model="usuario.cedula">
                                </b-form-group>
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Correo elctronico : " label-for="input-correo">
                                    <input id="input-correo" size="sm" type="text" class="form-control my-2" placeholder="Ingrese un correo elctronico" v-model="usuario.correo">
                                </b-form-group>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row mt-4 bg-success p-3">
                    <div class="col-12">
                        <div class="row">
                            <h5 class="text-white">Datos Inicio Sesin</h5>
                            <div class="col-6">
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Nombre Usuario : " label-for="input-usuario">
                                    <input id="input-usuario" size="sm" type="text" class="form-control my-2" placeholder="Ingrese un nombre de usuario para inicio de sesin" v-model="usuario.nombreUsuario">
                                </b-form-group>
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Password : " label-for="input-clave">
                                    <input id="input-clave" size="sm" type="password" class="form-control my-2" placeholder="ingrese una contrasea valida" v-model="usuario.clave">
                                </b-form-group>
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Rol : " label-for="input-rol">
                                    <input id="input-rol" size="sm" type="text" class="form-control my-2" placeholder="Ingrese un rol" v-model="usuario.rol">
                                </b-form-group>
                            </div>
                            <div class="col-6">
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Unidad : " label-for="input-unidad">
                                    <input id="input-unidad" size="sm" type="text" class="form-control my-2" placeholder="Ingrese la unidad del funcionario" v-model="usuario.unidad">
                                </b-form-group>
                                <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Sigla : " label-for="input-sigla">
                                    <input id="input-sigla" size="sm" type="text" class="form-control my-2" placeholder="Ingrese la sigla de la unidad del funcionario" v-model="usuario.sigla">
                                </b-form-group>
                            </div>
                        </div>
                    </div>
                </div>
                <b-button class="btn-warning my-4" type="submit">Agregar</b-button>
            </form>
        </b-card>
        <hr/>
        <div>
            <b-button class="mb-3 bg-danger" v-b-modal.modal-1>Consultar Usuarios</b-button>
            <b-modal id="modal-1" title="Usuarios Creados">
                <table class="table">
                    <thead>
                        <tr>
                            <th scope="col" style="font-size:10px">Id Usuario</th>
                            <th scope="col" style="font-size:10px">Nombre</th>
                            <th scope="col" style="font-size:10px">Apellido</th>
                            <th scope="col" style="font-size:10px">Cdula</th>
                            <th scope="col" style="font-size:10px">Usuario</th>
                            <th scope="col" style="font-size:10px">Sigla</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in usuarios" :key="index">
                            <th scope="row" style="font-size:10px">{{item._id}}</th>
                            <td style="font-size:10px">{{item.nombre}}</td>
                            <td style="font-size:10px">{{item.apellido}}</td>
                            <td style="font-size:10px">{{item.cedula}}</td>
                            <td style="font-size:10px">{{item.nombreUsuario}}</td>
                            <td style="font-size:10px">{{item.sigla}}</td>
                        </tr>
                    </tbody>
                </table>
            </b-modal>
        </div>
        
    </div>
</template>
<style scoped>

</style>

<script>
  export default {
    data() {
      return {
         usuarios : [],
         usuario : {nombre:"", apellido:"", cedula:"", correo:"", nombreUsuario:"", rol:"", unidad:"", sigla:""}
      }
    },
    created(){
        this.listarUsuarios();
    },
    methods: {
         agregarUsuario(){
            this.axios.post('/nuevo-usuario',this.usuario)
            .then(res =>{
                this.usuarios.push(res.data);
                this.usuario.nombre = "";
                this.usuario.apellido = "";
                this.usuario.cedula = "";
                this.usuario.correo = "";
                this.usuario.nombreUsuario = "";
                this.usuario.clave = "";
                this.usuario.rol = "";
                this.usuario.unidad = "";
                this.usuario.sigla = "";
            })
            .catch(e => {
                console.log(e.response);
            });

        },
        //listar usuarios masivos
        listarUsuarios(){
            this.axios.get('usuario')
            .then(res => {
                this.usuarios = res.data;
            })
            .catch(e => {
                console.log(e.response);
            })
        }
    }
}
</script>