<template>
    <div class="container">
        <h3>Crear Direccionamiento IPv6 Octeto <span class="text-danger">Unidad</span></h3>
        <b-card bg-variant="dark">
            <form @submit.prevent="editarOctetoUnidad(editarOcteto)" v-if="editar">
                <div class="row">
                    <div class="col-6">
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Sigla Unidad : " label-for="input-sigla-uno">
                            <b-form-input id="input-sigla-uno" size="sm" type="text" class="form-control my-2" placeholder="Ingresar la sigla de la unidad" v-model="editarOcteto.sigla"></b-form-input>
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Dependencia : " label-for="input-dependencia-uno">
                            <b-form-input id="input-dependencia-uno" size="sm" type="text" class="form-control my-2" placeholder="Ingresar la dependencia" v-model="editarOcteto.dependencia"></b-form-input>
                        </b-form-group>
                    </div>
                    <div class="col-6">
                        <b-form-group class="text-white" label-cols="1" label-cols-lg="5" label-size="sm" label="Tercer Octeto (unidad): " label-for="input-octeto-tres-uno">
                            <input id="input-octeto-tres-uno" size="sm" type="text" class="form-control my-2" placeholder="Ingresar el tercer octeto IPv6" v-model="editarOcteto.octetoTres">
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="" label-cols-lg="" label-size="sm" label="Cuarto Octeto (dependencia): " label-for="input-octeto-cuarto-uno">
                            <input id="input-octeto-cuarto-uno" size="sm" type="text" class="form-control my-2" placeholder="ingresar el cuarto octeto" v-model="editarOcteto.octetoCuatro">
                        </b-form-group>
                    </div>
                </div>
                <b-button class="btn-success my-2 mx-2" type="submit">Editar</b-button>
                <b-button class="my-2" type="submit" @click="editar=false">cancelar</b-button>
            </form>
            <form @submit.prevent="agregarOctetounidad()" v-if="!editar">
                <div class="row">
                    <div class="col-6">
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Sigla Unidad : " label-for="input-sigla">
                            <b-form-input id="input-sigla" size="sm" type="text" class="form-control my-2" placeholder="Ingresar la sigla de la unidad" v-model="unidad.sigla"></b-form-input>
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Dependencia : " label-for="input-dependencia">
                            <b-form-input id="input-dependencia" size="sm" type="text" class="form-control my-2" placeholder="Ingresar la dependencia" v-model="unidad.dependencia"></b-form-input>
                        </b-form-group>
                    </div>
                    <div class="col-6">
                        <b-form-group class="text-white" label-cols="1" label-cols-lg="5" label-size="sm" label="Tercer Octeto (unidad): " label-for="input-octeto-tres">
                            <input id="input-octeto-tres" size="sm" type="text" class="form-control my-2" placeholder="Ingresar el tercer octeto IPv6" v-model="unidad.octetoTres">
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="" label-cols-lg="" label-size="sm" label="Cuarto Octeto (dependencia): " label-for="input-octeto-cuarto">
                            <input id="input-octeto-cuarto" size="sm" type="text" class="form-control my-2" placeholder="ingresar el cuarto octeto" v-model="unidad.octetoCuatro">
                        </b-form-group>
                    </div>
                </div>
                <b-button class="btn-warning my-4" type="submit">Agregar</b-button>
            </form>
        </b-card>
        <div class="mt-4">
            <b-button v-b-modal.modalPopover>Mostrar Direccionamiento</b-button>
            <b-modal id="modalPopover" title="Direccionamiento IPv6 Octeto Unidad" ok-only>
                <table class="table">
                    <thead>
                        <tr>
                            <th scope="col" style="font-size:10px">Sigla</th>
                            <th scope="col" style="font-size:10px">Dependencia</th>
                            <th scope="col" style="font-size:10px">Octeto Tres</th>
                            <th scope="col" style="font-size:10px">Octeto Cuatro</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in unidades" :key="index">
                            <td style="font-size:10px">{{item.sigla}}</td>
                            <td style="font-size:10px">{{item.dependencia}}</td>
                            <td style="font-size:10px">{{item.octetoTres}}</td>
                            <td style="font-size:10px">{{item.octetoCuatro}}</td>
                            <td>
                                <b-button class="btn-success" size="sm" @click="activarEdicion(item._id)">Editar</b-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </b-modal>
        </div>
    </div>
</template>
<script>
    export default {
        data() {
            return {
                unidades : [],
                unidad : {sigla: "", dependencia:"", octetoTres:"", octetoCuatro:""},
                editar:false,
                editarOcteto:{}
            }
        },
        created(){
            this.listarOctetosUnidad();
        },
        methods: {
            agregarOctetounidad(){
                this.axios.post('/nuevo-octetounidad',this.unidad)
                .then(res =>{
                    this.unidades.push(res.data);
                    this.unidad.sigla = "";
                    this.unidad.dependencia = "";
                    this.unidad.octetoTres = "";
                    this.unidad.octetoCuatro = "";
                })
                .catch(e => {
                    console.log(e.response);
                });
            },
            //listar octetounidad
            listarOctetosUnidad(){
                this.axios.get('octetounidad')
                .then(res => {
                    this.unidades = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
            },
            activarEdicion(id) {
                this.editar = true;
                this.axios.get(`/octetounidad/${id}`)
                .then(res =>{
                    this.editarOcteto = res.data;
                })
                .catch(e =>{
                    console.log(e.response);
                });
            },
            //editar octeto unidad
            editarOctetoUnidad(item){
                this.axios.put(`/octetounidad/${item._id}`, item)
                .then(res =>{
                    const index = this.unidades.findIndex(n=> n._id===res.data._id);
                    this.unidades[index].sigla = res.data.sigla;
                    this.unidades[index].dependencia = res.data.dependencia;
                    this.unidades[index].octetoTres = res.data.octetoTres;
                    this.unidades[index].octetoCuatro = res.data.octetoCuatro;
                    this.editar =false;
                })
                .catch(e =>{
                    console.log(e.response);
                });
            }
        }
    }
</script>
