<template>
    <div class="container">
        <h3>Crear Direccionamiento IPv6 Octeto <span class="text-danger">Principal</span></h3>
        <b-card bg-variant="dark">
            <form @submit.prevent="editarOctetoprincipal(editarOcteto)" v-if="editar">
                <div class="row">
                    <div class="col-12">
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Entidad : " label-for="input-entidad">
                            <b-form-input id="input-entidad" size="sm" type="text" class="form-control my-2" placeholder="Ingresar la entidad" v-model="editarOcteto.entidad"></b-form-input>
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Primer Octeto : " label-for="input-octeto-1">
                            <input id="input-octeto-1" size="sm" type="text" class="form-control my-2" placeholder="Ingresar el primer octeto IPv6" v-model="editarOcteto.octetoUno">
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Segundo Octeto : " label-for="input-octeto-2">
                            <input id="input-octeto-2" size="sm" type="text" class="form-control my-2" placeholder="ingresar el segundo octeto" v-model="editarOcteto.octetoDos">
                        </b-form-group>
                    </div>
                </div>
                <b-button class="btn-success my-2 mx-2" type="submit">Editar</b-button>
                <b-button class="my-2" type="submit" @click="editar=false">cancelar</b-button>
            </form>
            <form @submit.prevent="agregarOctetoprincipal()" v-if="!editar">
                <div class="row">
                    <div class="col-12">
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Entidad : " label-for="input-entidad-uno">
                            <b-form-input id="input-entidad-uno" size="sm" type="text" class="form-control my-2" placeholder="Ingresar la entidad" v-model="octeto.entidad"></b-form-input>
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Primer Octeto : " label-for="input-octeto-uno">
                            <input id="input-octeto-uno" size="sm" type="text" class="form-control my-2" placeholder="Ingresar el primer octeto IPv6" v-model="octeto.octetoUno">
                        </b-form-group>
                        <b-form-group class="text-white" label-cols="2" label-cols-lg="4" label-size="sm" label="Segundo Octeto : " label-for="input-octeto-dos">
                            <input id="input-octeto-dos" size="sm" type="text" class="form-control my-2" placeholder="ingresar el segundo octeto" v-model="octeto.octetoDos">
                        </b-form-group>
                    </div>
                </div>
                <b-button class="btn-warning my-4" type="submit">Agregar</b-button>
            </form>
        </b-card>
        <div class="mt-4">
            <b-button v-b-modal.modalPopover>Mostrar Direccionamiento</b-button>
            <b-modal id="modalPopover" title="Direccionamiento IPv6 Primer Octeto" ok-only>
                <table class="table">
                    <thead>
                        <tr>
                            <th scope="col" style="font-size:10px">Entidad</th>
                            <th scope="col" style="font-size:10px">Octeto Uno</th>
                            <th scope="col" style="font-size:10px">Octeto Dos</th>
                            
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(item, index) in octetos" :key="index">
                            <td style="font-size:10px">{{item.entidad}}</td>
                            <td style="font-size:10px">{{item.octetoUno}}</td>
                            <td style="font-size:10px">{{item.octetoDos}}</td>
                            <td>
                                <b-button class="btn-success" size="sm" @click="activarEdicion(item._id)">Editar</b-button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </b-modal>
        </div>
    </div>
</template>
<script>
    export default{
        data() {
            return {
                octetos : [],
                octeto : {entidad: "", octetoUno:"", octetoDos:""},
                editar:false,
                editarOcteto:{}
            }
        },
        created(){
            this.listarOctetos();
        },
        methods:{
            agregarOctetoprincipal(){
                this.axios.post('/nuevo-octetoprincipal',this.octeto)
                .then(res =>{
                    this.octetos.push(res.data);
                    this.octeto.entidad = "";
                    this.octeto.octetoUno = "";
                    this.octeto.octetoDos = "";
                })
                .catch(e => {
                    console.log(e.response);
                });

            },
            //listar octetoprimero
            listarOctetos(){
                this.axios.get('octetoprincipal')
                .then(res => {
                    this.octetos = res.data;
                })
                .catch(e => {
                    console.log(e.response);
                })
            },
            activarEdicion(id) {
                this.editar = true;
                this.axios.get(`/octetoprincipal/${id}`)
                .then(res =>{
                    this.editarOcteto = res.data;
                })
                .catch(e =>{
                    console.log(e.response);
                });
            },
            //editar octeto principal
            editarOctetoprincipal(item){
                this.axios.put(`/octetoprincipal/${item._id}`, item)
                .then(res =>{
                    const index = this.octetos.findIndex(n=> n._id===res.data._id);
                    this.octetos[index].entidad = res.data.entidad;
                    this.octetos[index].octetoUno = res.data.octetoUno;
                    this.octetos[index].octetoDos = res.data.octetoDos;
                    
                    this.editar =false;
                })
                .catch(e =>{
                    console.log(e.response);
                });
            }

        }
    } 
</script>